.mint {
    background-image: url(../../img/mobile-back.png);

    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;



    .logo {
        width: 505px;
    }

    .mintdialog {
        width: 860px;
        height: auto;
        border-radius: 25px;
        background-color: rgba(0, 0, 0, 0.65);

        .modalback {
            background-image: url(../../img/dialogback.png);
            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;
            flex-direction: column;
            position: relative;
            background-size: 95%;
            background-repeat: no-repeat;
            background-position: center;
        }

        .price {
            position: absolute;
            left: 20px;
            top: 11px;
            font-family: "Segoe-UI", sans-serif;
            font-size: 28px;
        }

        .title {
            font-family: "28 Days Later", sans-serif;
            font-size: 62px;
            margin-top: 30px;
        }

        .subtitle {
            font-family: "28 Days Later", sans-serif;
            font-size: 30px;
            margin-top: -15px;

            span {
                font-size: 20px;
            }
        }

        .nftcount {
            width: 280px;
            height: 60px;
            border: 3px solid #FF049C;
            border-radius: 25px;
            margin-top: 58px;
            display: flex;
            align-items: center;
            justify-content: center;
            font-family: "Segoe-UI", sans-serif;
            font-size: 28px;
            font-weight: bold;
        }

        .connectbtn {
            cursor: pointer;
            margin-top: 25px;
            width: 280px;
            height: 60px;
            background-color: #A53E6F;
            border-radius: 25px;
            display: flex;
            align-items: center;
            justify-content: center;
            font-family: "Segoe-UI", sans-serif;
            font-size: 28px;
            font-weight: bold;
        }

        .description {
            margin-top: 15px;
            font-family: "Segoe-UI", sans-serif;
            font-size: 17px;
            opacity: 0.5;
        }

        .split {
            width: 683px;
            border-top: 5px solid #A53E6F;
            margin-top: 40px;
        }

        .remainTime {
            margin-top: 36px;
            font-family: "Segoe-UI", sans-serif;
            font-size: 28px;
            font-weight: bold;
            margin-bottom: 44px;
        }
    }

    .contact-group {
        margin-top: 27px;
        display: flex;
        align-items: center;
    }

    .footText {
        bottom: 20px;
        position: absolute;
        font-family: "Segoe-UI", sans-serif;
        font-size: 24px;
        font-weight: bold;
    }

    @media only screen and (max-width: 1536px) {
        .logo {
            width: 400px;
        }

        .mintdialog {
            width: 688px;

            .price {
                left: 16px;
                top: 9px;
                font-size: 22px;
            }

            .title {
                margin-top: 24px;
                font-size: 49px;
            }

            .subtitle {
                font-size: 24px;
            }

            .nftcount {
                margin-top: 47px;
                width: 224px;
                height: 48px;
                font-size: 22px;
            }

            .connectbtn {
                margin-top: 20px;
                width: 224px;
                height: 48px;
                font-size: 22px;
            }

            .description {
                margin-top: 12px;
                font-size: 13px;
            }

            .split {
                margin-top: 32px;
                width: 545px;
            }

            .remainTime {
                margin-top: 28px;
                font-size: 22px;
            }
        }
    }

    @media only screen and (max-width: 1440px) {
        .logo {
            width: 376px;
            margin-top: 38px;
        }

        .mintdialog {
            width: 645px;
            margin-top: 12px;

            .price {
                left: 15px;
                top: 9px;
                font-size: 20px;
            }

            .title {
                margin-top: 22px;
                font-size: 46px;
            }

            .subtitle {
                font-size: 22px;

                span {
                    font-size: 17px;
                }
            }

            .nftcount {
                margin-top: 43px;
                width: 210px;
                height: 45px;
                font-size: 20px;
            }

            .connectbtn {
                margin-top: 18px;
                width: 210px;
                height: 45px;
                font-size: 20px;
            }

            .description {
                margin-top: 11px;
                font-size: 12px;
            }

            .split {
                margin-top: 30px;
                width: 512px;
            }

            .remainTime {
                margin-top: 26px;
                font-size: 20px;
            }
        }
    }

    @media only screen and (max-width: 1366px) {
        .logo {
