            }

            .title {
                margin-top: 22px;
                font-size: 44px;
            }

            .subtitle {
                font-size: 21px;

                span {
                    font-size: 16px;
                }
            }

            .nftcount {
                margin-top: 41px;
                width: 200px;
                height: 43px;
                font-size: 19px;
            }

            .connectbtn {
                margin-top: 18px;
                width: 200px;
                height: 43px;
                font-size: 19px;
            }

            .description {
                margin-top: 10px;
                font-size: 11px;
            }

            .split {
                margin-top: 29px;
                width: 485px;
            }

            .remainTime {
                margin-top: 26px;
                font-size: 18px;
            }
        }
    }

    @media only screen and (max-width: 768px) {
        .logo {
