                font-size: 19px;
            }

            .connectbtn {
                margin-top: 18px;
                width: 200px;
                height: 43px;
                font-size: 19px;
            }

            .description {
                margin-top: 10px;
                font-size: 11px;
            }

            .split {
                margin-top: 29px;
