            }
        }
    }

    @media only screen and (max-width: 650px) {
        padding: 0px 18px;

        .logo {
            width: 50%;
        }

        .mintdialog {
            width: 100%;
            margin-top: 48px;

            .price {
                left: 13px;
                font-size: 14px;
            }

            .title {
                margin-top: 19px;
                font-size: 27px;
            }

            .subtitle {
                font-size: 16px;
                margin-top: -5px;

                span {
                    font-size: 10px;
                }
            }

            .nftcount {
                margin-top: 48px;
                width: 139px;
                height: 30px;
                font-size: 13px;
            }

            .connectbtn {
                width: 139px;
                height: 30px;
                font-size: 13px;
                margin-top: 12px;
            }

            .description {
                margin-top: 7px;
                font-size: 9px;
            }

            .split {
                margin-top: 19px;
                width: 80%;
                border-width: 3px;
            }

            .remainTime {
                margin-top: 18px;
                margin-bottom: 27px;
                font-size: 13px;
            }
        }

        .contact-group {
            margin-top: 21px;
        }

        .footText {
            font-size: 13px;
        }
    }

