.journey {
    padding-left: 60px;
    padding-right: 60px;

    .header {
        position: fixed;
        z-index: 2;
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-style: solid;
        border-width: 0px 0px 0px 0px;
        border-color: #FF049C;
        box-shadow: 0px 15px 15px -15px #ff049c;
        top: 0px;
        background-color: black;
        width: 100%;
        margin-left: -60px;
        margin-right: -60px;
        padding-left: 60px;
        padding-right: 60px;
        transition: background 0.3s, border 0.3s, border-radius 0.3s, box-shadow 0.3s;

        img {
            width: 200px;
        }

        .menu {
            display: none;
        }

        .mintbtn {
            display: none;
            font-family: 'Segoe-UI', sans-serif;
            font-size: 20px;
            line-height: 49px;
            color: white;
            // display: flex;
            align-items: center;
            background-color: #A53E6F;
            width: 253px;
            height: 68px;
            // display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 52px;
            font-weight: bold;

            svg {
                margin-left: 43px;
            }
        }

        .navbar {
            display: flex;
            align-items: center;

            .close {
                display: none;
            }

            a:not(:last-child) {
                margin-right: 90px;
                font-family: '28 Days Later', sans-serif;
                font-size: 35px;
                line-height: 43px;
                color: white;
            }

            a:selected {
                color: #A53E6F;
            }

            a:last-child {
                display: flex;
            }
        }
    }

    .section-1 {
        display: flex;
        justify-content: flex-end;
        margin-top: 30px;

        .contact-group {
            display: flex;
            align-items: center;
        }
    }

    .description {
        font-family: '28 Days Later', sans-serif;
        font-size: 43px;
        line-height: 52px;
        text-align: center;
        margin-top: -35px;
    }


    .section-2 {
        display: flex;
        margin-top: 80px;
        align-items: flex-end;

        .part-1 {
            width: 60%;
            margin-right: 50px;

            .title {
                font-family: '28 Days Later', sans-serif;
                font-size: 60px;
                line-height: 86px;
                text-align: center;
            }

            img {
                margin-top: 50px;
                box-shadow: 0px 0px 10px #FF049C;
                border: 15px solid #FF049C;
                border-radius: 25px;
            }
        }
    }

    .section-2 .part-2,
    .section-3,
    .section-5 .part-1,
    .section-5 .part-2 .part {
        box-shadow: 0px 0px 10px #FF049C;
        border: 15px solid #FF049C;
        border-radius: 25px;
        padding-top: 40px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        width: 50%;

        .title1 {
            display: flex;
            align-items: center;
            font-family: "Segoe UI", Sans-serif;
            font-weight: 700;
            font-size: 28px;
            margin-left: 14px;
            line-height: 37px;

            .rect {
                border-radius: 50%;
                width: 10px;
                height: 10px;
                background-color: white;
                margin-right: 10px;
                font-size: 18px;
            }

            .text {
                width: 100%;
            }
        }

        .content1 {
            margin-left: 37px;
            font-family: "Segoe UI", Sans-serif;
            font-weight: 400;
            margin-bottom: 33px;
            font-size: 28px;
            margin-right: 14px;
            margin-top: 6px;
        }

        .stage {
            border: 15px solid #FF049C;
            border-radius: 25px;
            margin-left: -15px;
            margin-right: -15px;
            background-color: #A53E6F;
            display: flex;
            justify-content: center;
            align-items: center;
            font-family: '28 Days Later', sans-serif;
            font-size: 59px;
            line-height: 71px;
            margin-bottom: -15px;
            position: relative;
            height: 150px;
            margin-top: 14px;

        }
    }

    .section-2,
    .part-2 {
        min-width: 40px;
    }

    .section-3 {
        width: 100%;
        margin-top: 50px;
        position: relative;

        img {
            position: absolute;
            width: 200px;
            top: -230px;
            right: 30px;
        }
    }

    .section-4 {
        border: 15px solid #A53E6F;
        border-radius: 25px;
        margin-top: 50px;
        background-image: url(../../img/background.png);
        background-attachment: fixed;
        background-position-y: 7%;
        background-repeat: no-repeat;
        background-size: cover;
        min-height: 400px;
    }

    .section-5 {
        display: flex;
        margin-top: 50px;

        .part-1 {
            margin-right: 50px;
            width: 879px;

            .content1 {
                margin-left: 34px;
            }
        }

        .part-2 {
            display: flex;
            flex-direction: column;
            min-width: 45%;

            .part {
                width: 100%;
                padding-top: 27px;

                .content1 {
                    margin-bottom: 14px;
                }
            }

            .title {
                font-family: '28 Days Later', sans-serif;
                font-size: 60px;
                line-height: 86px;
                height: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
            }
        }
    }

    .meet-team {
        margin-top: 43px;
        font-family: '28 Days Later', sans-serif;
        font-size: 60px;
        line-height: 86px;
        text-align: center;
    }

    .section-6,
    .section-7 {
        display: flex;
        justify-content: space-between;
        margin-top: 58px;

        .team-item {
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 180px;

            >img {
                width: 180px;
                height: 180px;
                border-radius: 50%;
            }

            .role {
                margin-top: 30px;
                font-family: 'Segoe-UI', sans-serif;
                font-size: 32px;
                line-height: 38px;
                font-weight: bold;
                white-space: nowrap;
            }

            a {
                margin-top: 15px;

                img {
                    width: 24px;
                }
            }
        }
    }

    .section-7 {
        justify-content: space-around;
    }

    .elementor-divider-separator:before {
        margin-left: -60px;
    }

    .elementor-divider-separator::after {
        margin-right: -60px;
    }

    .section-8 {
        display: none;
    }
}

