.landing {
	.section {
		position: relative;
		line-height: 0px;
		display: flex;
		justify-content: center;

		.logo {
			display: flex;
			align-items: center;
			position: absolute;
			left: 0px;
			top: -10px;

			img {
				width: 177px;
				height: 177px;
			}

			.logotitle {
				font-family: 'Edo', sans-serif;
				font-size: 30px;
				line-height: 40px;
			}
		}

		.banner {
			background-image: url(../../img/banner.png);
			background-position-y: center;
			background-repeat: no-repeat;
			background-size: cover;
			width: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
		}

		.mask1 {
			-webkit-mask-image: url(../../img/mask1.png);
			mask-image: url(../../img/mask1.png);
			-webkit-mask-size: 100%;
			mask-size: 100%;
			-webkit-mask-repeat: no-repeat;
			mask-repeat: no-repeat;
			-webkit-filter: drop-shadow(15px 15px 0 #a53e6f) drop-shadow(-15px -15px 0 #a53e6f);
			filter: drop-shadow(15px 15px 0 #a53e6f) drop-shadow(-15px -15px 0 #a53e6f);
			width: 92%;
		}

		.backgroundtext {
			position: absolute;
			font-size: 139px;
			font-family: 'Edo', sans-serif;
			width: 995px;
			height: 185px;
			line-height: 185px;
			text-align: center;
		}

		.parallax {
			/* The image used */
			background-image: url(../../img/background.png);

			/* Set a specific height */
			min-height: 90%;

			/* Create the parallax scrolling effect */
			background-attachment: fixed;
			background-position-y: 55%;
			background-repeat: no-repeat;
			background-size: cover;
		}
	}

	.section:not(:nth-child(2)) {
		margin-left: 60px;
		margin-right: 60px;
	}

	.journey {
		position: absolute;
		justify-content: center;
		display: flex;
		// margin-left: calc(100% - 8px);
		bottom: 0px;
		margin-left: 7px;
	}

	.journey div {
		cursor: pointer;
		position: relative;

	}

	.journey div img {
		position: absolute;
		top: -193px;
		width: 386px;
		left: -5px;
	}

	.journeybtn {
		background-color: #A53E6F;
		border-radius: 50px;
		font-size: 34px;
		line-height: 25px;
		color: white;
		/* width: auto; */
		font-family: "Monster AG", sans-serif;
		width: 380px;
		display: flex;
		height: 103px;
		text-align: center;
		align-items: center;
		justify-content: center;
	}

	.description {
		margin-top: 76px;
		display: flex;
		align-items: center;
		justify-content: center;

		img {
			width: 75%;
		}
	}

	.section-1 {
		margin-top: 64px;
		align-items: center;
	}

	.down img {
		width: 41px;
		height: 24px;
	}

	.section-1 .contact-group {
		position: absolute;
		display: flex;
		align-items: center;
		right: 0px;
	}

	.section-2 {
		display: flex;
		align-items: center;

		>img {
			width: 300px;
		}

		.mineral-help {
			margin-left: 100px;

			img {
				display: none;
			}
		}
	}

	.section-3 {
		border: 15px solid #A53E6F;
		border-radius: 25px;
		margin-top: 117px;
		background-image: url("../../img/background.png");
		background-attachment: fixed;
		background-position-y: 75%;
		background-repeat: no-repeat;
		background-size: cover;
		min-height: 400px;
	}

	.section-4 {
		align-items: center;
		margin-top: 100px;

		>img {
			width: 550px;
		}

		.mandrills-help img {
			display: none;
		}
	}


	.section-5 {
		align-items: center;
		margin-top: 100px;

		>img {
			width: 420px;
		}

		.participate-help {
			margin-left: 60px;

			img {
				display: none;
			}
		}
	}



	.section .help .title {
		font-family: '28 Days Later', sans-serif;
		font-size: 48px;
		line-height: 58px;
	}

	.section .help .content {
		margin-top: 40px;
		font-family: 'Segoe-UI', sans-serif;
		font-weight: bold;
		font-size: 24px;
		line-height: 32px;
	}

	@media only screen and (max-width: 1650px) {
		.journeybtn {
			font-size: 25px;
			width: 300px;
			height: 80px;
		}

		.journey div img {
			width: 300px;
			top: -145px;
		}

		.section .parallax {
			background-position-y: 59%;
		}

	}

