			}

			.participate-help {
				margin-left: 0px;

				.participate-header {
					display: flex;

					align-items: center;

					img {
						display: block;
						width: 81px;
					}

					.title {
						font-size: 27px;
						margin-left: 25px;
					}
				}
			}
		}
	}

	

